body.menu-open {
  overflow: hidden;
}

#menu__toggle {
  opacity: 0;
}

#menu__toggle:checked + .menu__btn > span {
  transform: rotate(45deg);
}

#menu__toggle:checked + .menu__btn > span:before {
  top: 0;
  transform: rotate(0deg);
}

#menu__toggle:checked + .menu__btn > span:after {
  top: 0;
  transform: rotate(90deg);
}

#menu__toggle:checked ~ .menu__overlay {
  display: block;
}

#menu__toggle:checked ~ .menu__box {
  right: 0;
}

.menu__btn {
  width: 30px;
  height: 26px;
  cursor: pointer;
  z-index: 1;
  position: fixed;
  top: 30px;
  right: 20px;
}

.menu__btn > span, .menu__btn > span:before, .menu__btn > span:after {
  width: 100%;
  height: 2px;
  background-color: #000;
  transition-duration: .25s;
  display: block;
  position: absolute;
}

.menu__btn > span:before {
  content: "";
  top: -12px;
}

.menu__btn > span:after {
  content: "";
  top: 12px;
}

.menu__overlay {
  background-color: rgba(34, 33, 33, .7);
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.menu__box {
  width: 70%;
  height: 100%;
  background-color: #fff;
  margin: 0;
  padding: 80px 0;
  list-style: none;
  transition-duration: .25s;
  display: block;
  position: fixed;
  top: 0;
  right: -100%;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, .4);
}

@media screen and (min-width: 768px) {
  .menu__box {
    width: 40%;
  }
}

.menu__item {
  color: #000;
  padding: 12px 24px;
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  transition-duration: .25s;
  display: block;
}

